  mounted(){
    var _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく
    
    //ソートの矢印が表示されるよう起動時にNoを昇順ソート
    this.mSortBy('id', 'check');

    // PT作成支援関連
    var target = null;
    var nikkeView = [];
    this.nikkes.forEach(function(nikke){ // ドラッグ開始
      nikkeView[nikke.num] = document.getElementById(nikke.id);
      nikkeView[nikke.num].addEventListener('dragstart', function(e){target = e.target;}, false);
      nikkeView[nikke.num].addEventListener('drag', function(){nikkeView[nikke.num].style.display = 'none';}, false);
    })

    document.addEventListener('dragover', function(e){ // ドラッグ中
      e.preventDefault(); // 操作のキャンセル
    }, false);
        
    document.addEventListener('drop', function(e){ // ドロップ
      e.preventDefault(); // 操作のキャンセル

      let drop = e.target; // ドロップ先

      // ドロップ先がニケ画像ならドロップ先をニケ画像の親に置き換え（部隊1～6か候補一覧のどちらかになる想定）
      if (e.target.id.startsWith('nikke')) {
        drop = e.target.parentNode;
      }

      // ドロップ先が部隊1～6
      if (drop.id.startsWith('squad')) {
        if (drop.childNodes.length < 6) {
          drop.appendChild(target); // 部隊に設定済みのニケが4人以下の場合はそのままドロップ
        } else if(drop.id == target.parentNode.id) {
          let idx = drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
          let cnt = 0;
          // ドロップ先の画像が部隊の枠の中の何番目の要素なのか探す
          drop.childNodes.forEach(function(item){
            if(item.id == e.target.id) {
              idx = cnt;
            }
            cnt++;
          });
          drop.insertBefore(target, drop.childNodes.item(idx)); // ドロップ先画像の1つ前に追加
        }
      // ドロップ先が候補一覧
      } if (drop.id == 'candidate') {
        drop.appendChild(target);
      }
      target.style.display = ''; // 表示状態に戻す
      target = null;
    }, false);
  
    this.updateMessageTimer = setInterval(this.updateMessage, 5000); // PT作成支援関連
  },

  methods: {
nikketest() {
  document.getElementById(this.nikkes[1].id).style.display = 'none';
},

mNikkeDbl(id) { // 部隊作成でニケ画像をダブルクリック
  let target = document.getElementById(id);
  // 部隊1～5上に画像がある場合は画像を候補へ移す
  if (target.parentNode.id.startsWith('squad')) {
    let candidate = document.getElementById('candidate');
    candidate.appendChild(target);
    //candidate.insertBefore(target, candidate.childNodes.item(0)); // 先頭に追加
  }
},

