        this.isAccConOpen = true;
        setTimeout(this.mNonAccConClickDelay, 50); //ここでfalse(閉じる)にしてもv-bindの挙動上うまく閉じないので一度明示的にtrueにしてから遅延でfalseにする
      }
    },

    mNonAccDivClickDelay() { // 区分絞り込みプルダウン以外クリックの遅延イベント
      this.isAccDivOpen = false;
    },

    mNonAccConClickDelay() { // 内容絞り込みプルダウン以外クリックの遅延イベント
      this.isAccConOpen = false;
    },

    packCheck(id) {
      var _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく
      this.packs.forEach(function(pack){
