  /* -----------------------------------------*/
  /* ラベル：トグルボタン */
  /* -----------------------------------------*/
  .toggle {
    display: inline-block;
    position: relative;
    width: 50px;
    height: 25px;
    border-radius: 50px;
    background-color: #dddddd;
    cursor: pointer;
    transition: background-color .4s;
    margin-top: 5px;
  }
  .toggle:has(:checked) {
    background-color: #3464ff;
  }
  .toggle::after {
    position: absolute;
    top: 0;
    left: 0;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    box-shadow: 0 0 5px rgb(0 0 0 / 20%);
    background-color: #fff;
    content: '';
    transition: left .4s;
  }
  .toggle:has(:checked)::after {
    left: 25px;
  }
  .toggleInput {
    display: none;
  }
  .toggle-td {
    width: 80px;
  }

