    line-height: 0.1;
    position: relative;
    display: inline-block;
    padding: 0.9rem 1rem;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    /*letter-spacing: 0.1em;*/
    color: #212529;
    border-radius: 0.6rem;  /* 角の丸み */
