  }
  .tooltipHelp p {
    display: flex;
    justify-content: center;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    bottom: -125px;
    left: 120px;
    transform: translateX(-50%);
    padding: .5em 1em;
    border-radius: 3px;
    background-color: #00000099;
    color: #fff;
    font-size: .7em;
