  .tooltipHelp:hover > p {
    visibility: visible;
    opacity: 1;
  }
  .tooltipHelp p::before {
    position: absolute;
    top: 0px;
    width: 0px;
    height: 0px;
    background-color: inherit;
    clip-path: polygon(10% 0, 0 100%, 100% 100%);
