    border-bottom: 2px solid #cccccc;
    border-radius: 0px; /* 枠の丸み */
  }
  .accordionDiv summary { /* タイトル部分 */
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: .1em .8em .2em .3em;
    color: #333333;
    cursor: pointer;
  }
  .accordionDiv summary::-webkit-details-marker {
    display: none;
  }
  .accordionDiv summary::after { /* 上下マーク部分 */
    transform: translateY(-25%) rotate(45deg);
    width: 7px;
    height: 7px;
    margin-left: 55px;
    border-bottom: 3px solid #b6bdc3;
    border-right: 3px solid #b6bdc3;
    content: '';
    transition: transform .3s;
  }
  .accordionDiv[open] summary::after {
